<template>
  <div class="app-container">
    <Header></Header>
    <Goods
      v-for="item in list"
      :key="item.id"
      :id="item.id"
      :title="item.goods_name"
      :pic="item.goods_img"
      :price="item.goods_price"
      :state="item.goods_state"
      :count="item.goods_count"
      @stateChange="changeStateData"
    ></Goods>
    <Footer
      :isFull="fullstate"
      :amount="amt"
      :numslct="numselected"
      @changefull="ctrla"
    ></Footer>
  </div>
</template>

<script>
import Header from "@/components/Header/Header.vue";
import axios from "axios";
import Goods from "@/components/Goods/Goods.vue";
import Footer from "@/components/Footer/Footer.vue";
import bus from "@/components/eventbus.js";
export default {
  components: {
    Header,
    Goods,
    Footer,
  },
  created() {
    this.initCartList();
    bus.$on("share", (addinfo) => {
      this.list.some((item) => {
        if (item.id === addinfo.id) {
          item.goods_count = addinfo.val;
          return true;
        }
      });
    });
  },
  data() {
    return {
      list: [],
    };
  },
  computed: {
    fullstate() {
      return this.list.every((item) => item.goods_state);
    },
    amt() {
      return this.list
        .filter((item) => item.goods_state)
        .reduce((total, item) => {
          return (total += item.goods_price * item.goods_count);
        }, 0);
    },
    numselected() {
      return this.list
        .filter((item) => item.goods_state === true)
        .reduce((sum, item) => {
          return (sum += item.goods_count);
        }, 0);
    },
  },
  methods: {
    async initCartList() {
      const { data: res } = await axios.get("https://www.escook.cn/api/cart ");
      if (res.status === 200) {
        this.list = res.list;
      }
    },
    changeStateData(e) {
      this.list.some((item) => {
        if (item.id === e.id) {
          item.goods_state = e.check;
          return true;
        }
      });
    },
    ctrla(val) {
      this.list.forEach((item) => (item.goods_state = val));
    },
  },
};
</script>

<style lang="less" scoped>
.app-container {
  padding-top: 45px;
  padding-bottom: 50px;
}
</style>
